export default ({name, data={}}) => {
	// promise
	return new Promise((resolve, reject) => {
		const loading = data.isLoading;
		data.isLoading && delete data.isLoading;
		// loading
		!loading && uni.showLoading({});
		// uniCloud
		uniCloud.callFunction({
			name,	// 
			data,
			success({result}) {	// 
				if (result.code === 0) {	// 
					resolve(result.data);
				} else {
					uni.showToast({	// 
						icon: "none",
						title: result.msg
					})
				}
			},
			fail(err) {	// 
				reject(err);
			},
			complete() {	// 
				!loading && uni.hideLoading();	// loading
			}
		})
	})
}